@import './../assets/styles/variables';

.pre__home {
   width: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   padding: 10px;
   .home {
      width: 100%;
      padding: 10px 20px;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      max-width: 800px;
      background-color: rgb($white, .05);
      position: relative;
      margin-top: 1%;
      border-radius: 10px;
      min-height: 95vh;
      
      .home__form {
         display: flex;
         justify-content: center;
         align-items: center;
   
         position:absolute;
         bottom:  10px;
         margin: 0 5%;
   
         width: 90%;
         height: 60px;
   
         border-radius: 10px;
         max-width: 800px;
      }
   }
}

.messages {
   display: flex;
   justify-content: flex-start;
   align-items: flex-start;
   flex-direction: column;
   width: 92%;
   padding-bottom: 80px;
}
.message {
   background-color: rgb($white, .1);
   border-radius: 6px;
   margin-bottom: 10px;
   padding: 6px 8px;
}